import React, { useEffect, useState } from 'react';
import { Link, useParams } from "react-router-dom";
import axios from "axios";
import {useNavigate} from "react-router-dom";
import {UploadButtonDiv, UploadDiv, UploadForm} from "../../Style/UploadCSS.js";

function Edit() {
    let params = useParams();
    const [PostInfo, setPostInfo] = useState({});
    const [Flag, setFlag] = useState(false);
    const [Title, setTitle] = useState(" ");
    const [Content, setContent] = useState(" ");
    let navigate = useNavigate();

    useEffect(() => {
        let body = {
            postNum : params.postNum
        }
        axios.post('/api/post/detail', body).then((response) => {
            if(response.data.success){
                setPostInfo(response.data.post);
                setFlag(true);
            }
        }).catch((err) => {
            console.log(err);
        })
      }, [])

    useEffect(() => {
        setTitle(PostInfo.title);
        setContent(PostInfo.content);
        }, [PostInfo])

      const onSubmit = (e) => {
        e.preventDefault();
        if((Title === "" || Content === "") || (Title === " " || Content === " ")){
          return alert("  ");
        }

        let body = {
          title : Title,
          content : Content,
          postNum : params.postNum,
        }

        axios.post("/api/post/edit", body).then((response) => {
          if(response.data.success){
            alert("  .");
            navigate(`/post/${params.postNum}`);
             
          } else{
            alert("  .");
          }
        }).catch((err) => {
          console.log(err);
        })
    };
  return(
  <UploadDiv>
    <UploadForm>
    <label htmlFor=""></label>
    <input id = 'title' type = 'text' value={Title} onChange={(event) => {
      setTitle(event.currentTarget.value);
      }} onClick={() => {
          if(Title === " " || "")
            setTitle("");
      }}></input>
      <label htmlFor="content"></label>
      <textarea
      value={Content} onChange={(event) => {
        setContent(event.currentTarget.value);
        }} onClick={() => {
          if(Content === " " || "")
              setContent("");
      }}></textarea>

        <UploadButtonDiv>
        <button className = "cancel" onClick={(e) => {
            e.preventDefault();
            navigate(-1);
            
            
        }}></button>
        <button onClick={(e) => onSubmit(e)}> </button>
        </UploadButtonDiv>

    </UploadForm> 
    

  </UploadDiv>);
}

export default Edit;
