
import React, { useState, useEffect } from "react";
import {useNavigate} from "react-router-dom";
import {UploadButtonDiv, UploadDiv, UploadForm} from "../../Style/UploadCSS.js";
import axios from "axios";
import ImageUpload from "./ImageUpload.js";
import {useSelector} from "react-redux";


function Upload(props) {
    const [Title, setTitle] = useState(" ");
    const [Content, setContent] = useState(" ");
    const [Image, setImage] = useState("");
    let navigate = useNavigate();
    const user = useSelector((state) => state.user);
    
    useEffect(() => {
      if(!user.accessToken){
        alert("    .");
        navigate("/login");
      }
    },[])

    const onSubmit = (e) => {
        e.preventDefault();
        if((Title === "" || Content === "") || (Title === " " || Content === " ")){
          return alert("  ");
        }

        let body = {
          title : Title,
          content : Content,
          image : Image,
          uid : user.uid,
        }

        axios.post("/api/post/submit", body).then((response) => {
          if(response.data.success){
            alert("  .");
            navigate("/");
             
          } else{
            alert("  .");
          }
        }).catch((err) => {
          console.log(err);
        })
    };

  return( 
  <UploadDiv>
    <UploadForm>
    <label htmlFor=""></label>
    <input id = 'title' type = 'text' value={Title} onChange={(event) => {
      setTitle(event.currentTarget.value);
      }} onClick={() => {
          if(Title === " " || "")
            setTitle("");
      }}></input>
      <ImageUpload setImage={setImage}></ImageUpload>
      <label htmlFor="content"></label>
      <textarea
      value={Content} onChange={(event) => {
        setContent(event.currentTarget.value);
        }} onClick={() => {
          if(Content === " " || "")
              setContent("");
      }}></textarea>

    <UploadButtonDiv><button onClick={(e) => onSubmit(e)}></button></UploadButtonDiv>

    </UploadForm> 
    

</UploadDiv>);


}

export default Upload;
