import React, {useState, useEffect, useRef} from 'react';
import { RepleContentDiv, RepleUploadDiv} from "../../Style/RepleCSS.js" 
import { useSelector } from 'react-redux';
import axios from 'axios';
import Avatar from "react-avatar";

function RepleContent(props) {

    const [ModalFlag, setModalFlag] = useState(false);
    const [EditFlag, setEditFlag] = useState(false);
    const [Reple, setReple] = useState(props.reple.reple);
    const ref = useRef();
    const user = useSelector((state) => state.user);
    useOnClickOutside(ref, () => setModalFlag(false));

    const SubmitHandler = (e) => {
        e.preventDefault();
        let body = {
            uid : user.uid,
            reple : Reple,
            postId : props.reple.postId,
            repleId : props.reple._id,
        }

        axios.post("/api/reple/edit", body).then((response) => {
            if(response.data.success){
                alert("  .");
            }else{
                alert("  .");
            }
            return window.location.reload();
        })
    };

    const DeleteHandler = (e) => {
        e.preventDefault();
        if(window.confirm(" ?")){
            let body = {
                repleId : props.reple._id,
                postId : props.reple.postId,
            }
            axios.post('/api/reple/delete', body).then((response) => {
                if(response.data.success){
                    alert(" .");
                    window.location.reload();
                }
            }).catch((err) => {
                alert("  .");
            })
          }
    }

    return(
        
            <RepleContentDiv>
                <div className='author'>
                    <div className='userInfo'>
                        <Avatar size = "30" round ={true} src ={props.reple.author.photoURL} style ={{border : "1px solid #c6c6c6"}}></Avatar>
                        <p className='Name'>{props.reple.author.displayName}</p>
                    </div>
                    {
                        props.reple.author.uid === user.uid && <div className='modalControl'>
                        <span onClick={() => setModalFlag(true)}>...</span>
                        {ModalFlag && (<div className='modalDiv' ref={ref}>
                            <p onClick={() => {
                                setEditFlag(true);
                                setModalFlag(false);
                                }}></p>
                            <p className='delete' onClick={(e) => DeleteHandler(e)}></p>
                        </div>)}
                    </div>
                    }
                </div>
                {EditFlag ? (
                    <RepleUploadDiv>
                    <form>
                    <input type = "text" value={Reple} onChange={(e) => {
                        setReple(e.currentTarget.value);
                    }} ></input>
                    <button onClick={(e) => {SubmitHandler(e)}}></button>
                    </form>
                    <div className='cancel'>
                        <button onClick={(e) => 
                            {
                            e.preventDefault(); 
                            setEditFlag(false)
                        }
                            }></button>
                    </div>
                </RepleUploadDiv>
                ) : <p>{props.reple.reple}</p>}
            </RepleContentDiv>
        
    );
}

function useOnClickOutside(ref, handler) {
    useEffect(
      () => {
        const listener = (event) => {
          if (!ref.current || ref.current.contains(event.target)) {
            return;
          }
          handler(event);
        };
        document.addEventListener("mousedown", listener);
        document.addEventListener("touchstart", listener);
        return () => {
          document.removeEventListener("mousedown", listener);
          document.removeEventListener("touchstart", listener);
        };
      },
      [ref, handler]
    );
  }
export default RepleContent;
