import React, { useState, useEffect } from 'react';
import LoginDiv from '../../Style/UserCSS';
import firebase from '../../firebase.js';
import axios from 'axios';
import { useNavigate } from 'react-router-dom';
import {useSelector} from "react-redux";


function Register() {
  const [Name, setName] = useState("");
  const [Email, setEmail] = useState("");
  const [PW, setPW] = useState("");
  const [PWConfirm, setPWConfirm] = useState("");
  const [Flag, setFlag] = useState(false);
  const [NameCheck, setNameCheck] = useState(false);
  const [NameInfo, setNameInfo] = useState("");
  const user = useSelector((state) => state.user);
  let navigate = useNavigate();

  useEffect(() => {
    if(user.accessToken){
        navigate("/");
      }
  },[])

  const RegisterFunc = async (e) => { //async , await ( :   )
    setFlag(true);
    e.preventDefault();
    if(!(Name && Email && PW && PWConfirm)){
        return alert("  .");
    }
    if(PW != PWConfirm){
        return alert("     .");
    }
    if(!NameCheck){
        return alert("  .")
    }
    let createdUser = await firebase
    .auth()
    .createUserWithEmailAndPassword(Email, PW);

    await createdUser.user.updateProfile({
        displayName : Name,
        photoURL : "https://kr.object.ncloudstorage.com/react-community/user/profile.png",
    });

    console.log(createdUser.user);
    let body = {
        email : createdUser.user.multiFactor.user.email,
        displayName : createdUser.user.multiFactor.user.displayName,
        uid : createdUser.user.multiFactor.user.uid,
        photoURL : "https://kr.object.ncloudstorage.com/react-community/user/profile.png",

    };
    axios.post("/api/user/register", body).then((response) => {
        setFlag(false);
        if(response.data.success){
            // 
            navigate("/");
        }else{
            // 
            return alert(" ");
        }
    })
  }

  const NameCheckFunc = async (e) => {
    e.preventDefault();
    if(!Name){
        return alert(" .");
    }
    let body = {
        displayName : Name,
    }
    await axios.post("/api/user/namecheck", body).then((response) => {
        if(response.data.success){
            if(response.data.check){
                setNameCheck(true);
                setNameInfo("  .");
            }else{
            setNameInfo("  .")}
        }
    });
  }
  

  return(
    <LoginDiv>
        <form>
        <label></label>
        <input type = "name" value={Name} onChange = {(e) => setName(e.currentTarget.value)} disabled = {NameCheck}></input>
        {NameInfo}
        <button onClick={(e) => NameCheckFunc(e)}> </button>
        <label></label>
        <input type = "email" value={Email} onChange = {(e) => setEmail(e.currentTarget.value)}></input>
        <label></label>
        <input type = "password" minLength={8} value={PW} onChange = {(e) => setPW(e.currentTarget.value)}></input>
        <label> </label>
        <input type = "password" minLength={8} value={PWConfirm} onChange = {(e) => setPWConfirm(e.currentTarget.value)}></input>
        <button disabled={Flag} onClick={(e) => RegisterFunc(e)}></button>
        </form>
    </LoginDiv>
  ); 
}

export default Register;
